<template>
  <div class="conbox fix">
    <div v-if="curpage == 'list'">
      <!-- <div class="conbox fix" style="background-color: #f8e0accb">
        <img
          src="/src/assets/img/bhnxy.png"
          style="float: left; width: 70%; height: 140px; margin: 0 calc(15%)"
        />
      </div> -->
      <ul>
        <li v-for="(item, index) in list">
          <div>
            <a href="#" class="row l" @click="toDetail(item.id)">
              <h2>{{ item.practiceName }}</h2>
              <p v-html="item.remark" class="remark"></p>
              <span>{{ item.publishDate }}</span>
            </a>
            <img
              :src="item.photoUrl"
              style="float: left; width: 20%; height: 140px"
            />
          </div>
        </li>
      </ul>
    </div>
    <div v-if="curpage == 'detail'">
      <div class="zy_content fix">
        <div class="zy_conbox">
          <div class="zy_newsxq">
            <div class="xq_tit bdb_d">
              <h1>
                {{ article.practiceName }}
              </h1>
              <p>
                <span>实践团队：{{ article.teamName }}</span>
                <span>发布时间：{{ article.publishDate }}</span>
              </p>
            </div>
            <div class="txt">
              <p v-html="article.intro"></p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      curpage: "list",
      list: [],
      article: {},
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    loadData: function () {
      axios.get("/api/index/practice/page", {}).then((d) => {
        if (d.data.code == 200) {
          let result = d.data.data;
          this.list = result.list;
        }
      });
    },
    toDetail: function (id) {
      axios.get("/api/index/practice/get?id=" + id).then((d) => {
        this.curpage = "detail";
        this.article = d.data.data;
      });
    },
  },
};
</script>

<style scoped>
ul li {
  margin: 30px;
  height: 150px;
  border-bottom: 1px solid #d4d4d4;
}

.row {
  width: 75%;
  margin-right: 15px;
}
.row h2 {
  font-weight: 650;
  font-style: normal;
  font-size: 18px;
  color: #666666;
  line-height: 18px;
  margin-bottom: 10px;
}
.row p {
  font-size: 16px;
  color: #656565;
  line-height: 23px;
  text-align: justify;
}
.row span {
  font-size: 13px;
  color: #9b9b9b;
}
.remark {
  height: 100px;
  overflow: hidden;
  line-height: 20px;
  text-overflow: ellipsis;
}
</style>



